<template>
  <view class="page">
    <view class="search">
    	<way-search :inputValue="articleKeyword" @searchHandle="searchHandle" :focus="true" placeholder="请输入商品名"></way-search>
    </view>
    <template v-if="historyListShow">
      <view class="history-title">
        <view class="">
          最近搜索
        </view>
        <u-icon name="trash" size="40" color="#AFAFAF" @click="delHistory" />
      </view>
      <view class="history-box">
        <view class="history-item" v-for="(item, index) in historyList" :key="index" @click="searchKey(item)">
          {{item}}
        </view>
      </view>
    </template>
	<view class="shop-box">
		<view class="shop-item" v-for="(item, index) in shopList" :key="index">
			<u-image width="160rpx" height="120rpx" :src="item.imageSrc"></u-image>
			<view class="header-title">
				<view class="title">
					{{ item.title }}
				</view>
				<view class="desc">
					{{ item.desc }}
				</view>
				<view class="price">
					￥{{ item.price }}
				</view>
			</view>
		</view>
	</view>
	<u-empty v-if="shopList.length <= 0" mode="search" margin-top="100"></u-empty>
  </view>
</template>

<script>
export default {
  data() {
    return {
      articleKeyword: '',
      historyList: [],
	  historyListShow: false,
	  shopList: [{
	  		imageSrc: require('@/assets/imgs/shop.jpeg'),
	  		title: 'DJI Air',
	  		desc: '航拍轻旗舰，性能均衡',
			price: '233'
	  	},
	  	{
	  		imageSrc: require('@/assets/imgs/shop.jpeg'),
	  		desc: '准专业级影像系统，致敬经典准,专业级影像系统，致敬经典',
			title: 'DJI Avata',
			price: '233'
	  	},
	  	{
	  		imageSrc: require('@/assets/imgs/shop.jpeg'),
			desc: '准专业级影像系统，致敬经典准,专业级影像系统，致敬经典',
	  		title: 'DJI Avata',
			price: '233'
	  	}],
    };
  },
  onShow() {
    this.historyList = uni.getStorageSync('historySearch') ? uni.getStorageSync('historySearch') : []
	if (this.historyList && this.historyList.length > 0) {
		this.historyListShow = true
	}
  },
  methods: {
    searchHandle(e) {
      console.log(e, 'eee')
      if (!this.historyList.includes(e)) {
        this.historyList.unshift(e)
        uni.setStorageSync('historySearch', this.historyList)
		this.historyListShow = false
      }
    },
    delHistory() {
      uni.removeStorageSync('historySearch')
      this.historyList = []
    },
    searchKey(item) {
      console.log(item, 'item')
      this.articleKeyword = item
    }
  }
}
</script>

